<template>
  <echart :option="option" />
</template>
<script>
import Echart from "@/components/Echart.vue";
export default {
  data() {
    return {
      dataShadow: [1, 2, 3, 4],
      yMax: 500,
      dataAxis: [
        "点",
        "击",
        "柱",
        "子",
        "或",
        "者",
        "两",
        "指",
        "在",
        "触",
        "屏",
        "上",
        "滑",
        "动",
        "能",
        "够",
        "自",
        "动",
        "缩",
        "放",
      ],
      num: [
        220,
        182,
        191,
        234,
        290,
        330,
        310,
        123,
        442,
        321,
        90,
        149,
        210,
        122,
        133,
        334,
        198,
        123,
        125,
        220,
      ],
    };
  },
  computed: {
    option() {
      return {
        title: {
          text: "来源统计",
          left: "center",
          textStyle: {
            //文字的样式
            color: "#ccc",
            fontSize: 16,
          },
        },
        xAxis: {
          data: this.dataAxis,
          axisLabel: {
            inside: true,
            textStyle: {
              color: "#fff",
            },
          },
          axisTick: {
            show: false,
          },
          axisLine: {
            show: false,
          },
          z: 10,
        },
        yAxis: {
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          axisLabel: {
            textStyle: {
              color: "#999",
            },
          },
        },
        dataZoom: [
          {
            type: "inside",
          },
        ],
        series: [
          {
            // For shadow
            type: "bar",
            itemStyle: {
              color: "rgba(0,0,0,0.05)",
            },
            barGap: "-100%",
            barCategoryGap: "40%",
            data: this.dataShadow,
            animation: false,
          },
          {
            type: "bar",
            // itemStyle: {
            //   color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            //     { offset: 0, color: "#83bff6" },
            //     { offset: 0.5, color: "#188df0" },
            //     { offset: 1, color: "#188df0" },
            //   ]),
            // },
            // emphasis: {
            //   itemStyle: {
            //     color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            //       { offset: 0, color: "#2378f7" },
            //       { offset: 0.7, color: "#2378f7" },
            //       { offset: 1, color: "#83bff6" },
            //     ]),
            //   },
            // },
            data: this.num,
          },
        ],
      };
    },
  },
  mounted() {
    this.$unlock();
    this.play();
  },
  methods: {
    play() {
      this.dataShadow = this.num.map(() => this.yMax);
    },
  },
  components: {
    Echart,
  },
};
</script>

